.chat .header {
  padding: 0 10px;
  background: #7DBBE9;
  font-size: 20px;
  font-weight: 400;
}

.chat .content {
  padding: 10px;
  background: #fff;
}

.chat .message {
  margin: 10px 0;
}

.chat .avatar {
  text-align: center;
  font-size: 24px;
  border: 1px solid;
  border-radius: 50%;
  width: 38px;
  height: 100%;
  background-color: #399FE9;
  color: #fff;
}

.chat .left-tip {
  position: absolute;
  top: 0;
  left: 0;
  width: 0;
  height: 0;
  border-top: 10px solid transparent;
  border-right: 20px solid #F4F5F4;
  border-bottom: 10px solid transparent;
  border-left: 0px solid transparent;
}

.chat .right-tip {
  position: absolute;
  top: 0;
  right: 0;
  width: 0;
  height: 0;
  border-top: 10px solid transparent;
  border-right: 0px solid transparent;
  border-bottom: 10px solid transparent;
  border-left: 20px solid #F4F5F4;
}

.chat .cont {
  margin: 0 20px;
  background-color: #F4F5F4;
  padding: 5px;
  border-radius: 5px;
}

.chat .bottom-sender {
  position: fixed;
  bottom: 0;
  left: 0;
  padding: 10px;
  width: 100%;
  background-color: #7DBAE8;
}

.chat .place-occupation {
  width: 100%;
  height: 70px;
}
